body {
  overflow-x: hidden;
}
body header.top-navigation .dropdown-menu {
  margin-top: 1rem;
}
body header.top-navigation .search-navbar .select2-selection .select2-selection--single {
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection {
  background-color: transparent;
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection--single .select2-selection__rendered {
  color: #212529;
  padding-left: 8px;
  padding-right: 8px;
}
body header.top-navigation .navbar-toggler {
  border: none;
}
body main {
  padding-top: 56px;
}
body main aside.left-navigation {
  background-color: #fff;
  z-index: 4;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  padding: 62px 0 0;
  transition: all 0.25s;
  overflow-y: auto;
}
body main aside.left-navigation ul {
  padding: 0.5em 0.5em 0;
}
body main aside.left-navigation ul li {
  list-style: none;
  line-height: 2.5rem;
  margin-bottom: 0.25rem;
}
body main aside.left-navigation ul li:hover {
  color: #6FEDD6;
  border-radius: 8px;
}
body main aside.left-navigation ul li a {
  display: block;
  text-decoration: none;
  color: #000;
}
body main aside.left-navigation ul li.active a.active {
  border-radius: 8px;
  background-color: #2C3333;
  color: #fff;
}
body main section.content {
  padding: 1rem 1rem 0 1rem;
  margin-left: 200px;
  transition: all 0.1s;
}
body main .card {
  border-radius: 0;
}
body main .card .kv-panel-before {
  border-bottom: none;
}
body main .card .card-header {
  border: none;
}
body main .card .card-footer {
  border-top: none;
  background: transparent;
  padding: 0;
}
body main .card table {
  border-collapse: collapse;
  margin-bottom: 0;
}
body main .card table td, body main .card table th {
  border: 1px solid #ced4da;
}
body main .card table tr {
  border: 0;
}
body main .card table tr:first-child th {
  border-top: 1px solid #ced4da;
}
body main .card table tr:last-child td {
  border-bottom: 0;
}
body main .card table tr td:first-child,
body main .card table tr th:first-child {
  border-left: 0;
}
body main .card table tr td:last-child,
body main .card table tr th:last-child {
  border-right: 0;
}
body main .card table.table-detail-view th, body main .card table.table-detail-view td {
  border: 1px solid #ced4da;
}
body main .card table.table-detail-view tr {
  border: 0;
}
body main .card table.table-detail-view tr:last-child th, body main .card table.table-detail-view tr:last-child td {
  border-bottom: 0;
}
body main ul.pagination {
  margin: 0 0 auto;
}
body footer {
  padding: 0.75rem 1rem;
  margin-left: 200px;
  border-top: 1px solid #e9ecef;
}

/* all screen include ipad mini */
@media only screen and (min-width: 768px) {
  body main aside.left-navigation {
    width: 200px;
    min-height: 100vh;
    z-index: 1;
    border-right: 1px solid #e9ecef;
  }
}
/* Mainstream tablet */
@media (max-width: 767px) {
  body main aside.left-navigation {
    width: 0;
    min-height: 100vh;
    z-index: 1;
    border-right: none;
  }
  body main section.content {
    margin-left: 0;
  }
  body footer {
    margin-left: 0;
  }
}
body.dark {
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark main aside.left-navigation {
  border-right: none;
  background: #343a40;
  box-shadow: -5px -5px 10px #2f343a, 5px 5px 10px #394046;
}

body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer {
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark div.card {
  border: 1px solid #fff;
}

body.dark .dropdown-menu {
  border: 1px solid #fff !important;
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark a, body.dark i.bi {
  color: #fff !important;
}

body.dark .dropdown-item:hover {
  background-color: #2C3333 !important;
}

body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}

body.dark footer {
  border-top: 0;
}

@media print {
  * {
    font-family: "Arial", serif;
    font-weight: bold;
  }
  @page {
    /* Ukuran kertas */
    size: 21.5cm 14cm; /* Lebar */
    margin: 0.5cm 0.2cm 0 0.25cm; /* Left */
  }
  body {
    font-size: 11pt;
  }
  body .table {
    width: 100%;
    border-collapse: collapse;
    font-size: 11pt;
  }
  body .table th {
    text-align: left;
  }
  body .table th, body .table td {
    padding: 1pt 2pt;
    border: 1pt solid black;
  }
  body .table td {
    letter-spacing: 0.05em;
  }
  body .table thead tr th {
    text-align: left;
  }
  body .table thead tr th.text-end {
    text-align: right;
  }
  body .table.table-detail-view {
    width: 50%;
  }
  body .card {
    margin-top: 6pt;
    padding: 6pt 0;
  }
  body .card .table th, body .card .table td {
    padding: 5pt 0;
    border: none;
  }
  body .card .table.table-grid-view th, body .card .table.table-grid-view td {
    padding: 5pt;
    border: 1pt solid #000;
  }
  body .card .table.table-grid-view thead tr th {
    text-align: center;
  }
  body .text-start {
    text-align: left !important;
  }
  body .text-center {
    text-align: center !important;
  }
  body .text-end {
    text-align: right !important;
  }
  body .border-bottom {
    border: 1px !important;
  }
  body .border-0 {
    border: 0 !important;
  }
  body .border-start-0 {
    border-left: 0 !important;
  }
  body .border-end-0 {
    border-right: 0 !important;
  }
  body .border-top-0 {
    border-top: 0 !important;
  }
  body .border-bottom-0 {
    border-bottom: 0 !important;
  }
  body .p-0 {
    padding: 0;
  }
  body .m-0 {
    margin: 0;
  }
  body .mt-1 {
    margin-top: 1rem !important;
  }
  body .mt-2 {
    margin-top: 1.5rem !important;
  }
  body .mt-3 {
    margin-top: 1.5rem !important;
  }
  body .mt-5 {
    margin-top: 2.5rem !important;
  }
  body .font-weight-bold {
    font-weight: bold;
  }
  body .text-nowrap {
    white-space: nowrap;
  }
}
.user-photo-32 {
  border-radius: 50%;
  object-fit: cover;
}